    ],
      text: '',
      form: {
        id: '',
        email: '',
        first_name: '',
        last_name: '',
        avatar: ''
      },
      newFindName: [],
      newListAll: []
    }
  },
  mounted() {
    if (localStorage.getItem('key')) {
      try {
        this.listAll = JSON.parse(localStorage.getItem('key'));
      } catch (e) {
        localStorage.removeItem('key');
      }
    }
  },
  methods: {
    deleteUser(index) {
      this.$delete(this.listAll, index)
      localStorage.clear()
      localStorage.setItem('key', JSON.stringify(this.listAll))
      if (this.listAll.length === 0) {
        this.listAll.unshift(this.form)
        this.listAllStatus = false
      }
    },
    deleteUserFindName(index) {
      this.$delete(this.newFindName, index)
    },
    goOverToPage(index) {
      this.pageStatus = !this.pageStatus
      this.listAllStatus = !this.listAllStatus
      this.searchStatus = !this.searchStatus
      this.addUserButtonStatus = !this.addUserButtonStatus
      this.addListStartStatus = !this.addListStartStatus
      this.pageIndex = index
    },
    returnList() {
      this.pageStatus = !this.pageStatus
      this.listAllStatus = !this.listAllStatus
      this.searchStatus = !this.searchStatus
      this.addUserButtonStatus = !this.addUserButtonStatus
      this.addListStartStatus = !this.addListStartStatus
      this.pageIndex = 0
    },
    addListStart() {
      this.listAll = this.listAllStart
      localStorage.clear()
      localStorage.setItem('key', JSON.stringify(this.listAll))
      this.listAllStatus = true
    },
    addUser() {
      this.addUserStatus = !this.addUserStatus
      if (this.listAll.length === 0) {
        this.listAll.unshift(this.form)
      }
      this.form.id = ''
      this.form.first_name = ''
      this.form.last_name = ''
      this.form.email = ''
      this.form.avatar = ''
    },
    onSubmit() {
      this.form.id = this.listAll.length + 1
      this.listAllStatus = true
      this.listAll.push(this.form)
      for (let i = 0; i < this.listAll.length - 1; i++) {
        if (this.listAll[i].first_name === this.listAll[i + 1].first_name && this.listAll[i].last_name === this.listAll[i + 1].last_name
            && this.listAll[i].email === this.listAll[i + 1].email && this.listAll[i].avatar === this.listAll[i + 1].avatar)
        {
          this.newListAll = this.listAll.shift()
        }
      }
      this.addUserStatus = !this.addUserStatus
      localStorage.clear()
      localStorage.setItem('key', JSON.stringify(this.listAll))
      this.listAll = JSON.parse(localStorage.getItem('key'));
    },
    FindNameAll() {
      for (let i = 0; i < this.listAll.length; i++) {
        if (this.listAll[i].first_name.toLowerCase().includes(this.findName.toLowerCase()) && this.findName.length > 1) {
          this.newFindName.unshift(this.listAll[i])
          this.listAll.splice(i, 1)
          this.newFindName[0].id = this.listAll.length + 1
        }
      }
      if (this.newFindName.length > 1) {
        this.newFindName[0].id = this.listAll.length + 2
        this.listAll.unshift(this.newFindName[1])
        this.newFindName.splice(1, 1)
      }
    }
  },
}
