import Vue from 'vue'
import VueRouter from 'vue-router'
import List from '../views/List'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'List',
    component: List
  },
  {
    path: '/RangeInput',
    name: 'RangeInput',
    component: () => import(/* webpackChunkName: "about" */ '../views/RangeInput')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
